.write{
    padding-top: 50px;
}

.writeForm{
   position: relative;
}

.writeFormGroup{
    margin-left: 150px;
}

.writeIcon{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    border: 1px solid;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: darkcyan;
    cursor: pointer;
}

.writeInput{
    font-size: 30px;
    border: none;
    padding: 20px;
    width: 70vw;

}

.writeInput:focus{
    outline: none;
}

.writeText{
    font-size: 20px;
}

.writeSubmit{
    position: absolute;
   top:20px;
   right: 55px;
   color: white;
   background-color: teal;
   padding: 10px;
   border: none;
   border-radius: 10px;
   cursor: pointer;
   font-size: 16px;
}

.writeImg{
    margin-left: 150px;
    width: 70vw;
    height: 450px;
    border-radius: 10px;
    object-fit: cover;
}